.scene {
  width: 100%;
  max-width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  max-height: 100vh;
  max-height: calc(var(--vh, 1vh) * 100);
}

.scene > .inner {
  display: flex;
  padding: 0 13%;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1880px;
  height: 100%;
  margin: auto;
  transition: opacity 0.25s cubic-bezier(0.22,0.61,0.36,1);
}

.scene:not(.visible) * {
  animation-play-state: paused;
}
